                    let allData         = response.data;
                    let finalArray:ALIGN[]  = [];
                    if(allData.length > 0){
                        allData.forEach((elem:ALIGN)=>{
                            let allRARIO    = JSON.parse(elem.link_radio??`{"tx": 0, "rx":0}`);
                            let RADIOrx       = (typeof allRARIO['rx']).toUpperCase() == 'OBJECT' && allRARIO['rx'] !== null  ? allRARIO['rx'][0] : allRARIO['rx'];
                            // let RADIOtx       = (typeof allRARIO['tx']).toUpperCase() == 'OBJECT' ? allRARIO['tx'][0] : allRARIO['tx'];
                            let allAVG      = JSON.parse(elem.avgpower??`{"tx": 0, "rx":0}`);
                            let AVGtx       = (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null  ? allAVG['tx'][0] : allAVG['tx'];
                            let finalItem = {
                                fecha           : elem.fecha,
                                name            : elem.name,
                                ip              : elem.ip,
                                avgpower        : AVGtx??0,
                                link_radio_rx   : RADIOrx??0,
                                tipo            : elem.tipo??'',
                            }
                            finalArray.push(finalItem) 
                        })
                        items.value = finalArray;
                    }
                    else{  showError.value = true }
                }
                

                else if(getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.temp_inst){
                    let itemsTemp = response.data;
                    let finalArray:SNR[]  = [];
                    itemsTemp.forEach((elem:KPI) => {
                        let temp = JSON.parse(elem.valores??'');
                        let finalItems = {
                            fecha      : elem.fecha,
                            ip         : elem.ip,
                            name       : elem.name,
                            temperatura: `${(temp.temperature[0]/100).toFixed(2)} °C`
                        }
                        finalArray.push(finalItems);
                    })
                    items.value = finalArray;
                    items.value.length > 0 ? showError.value = false : showError.value = true;
                    // console.log('hoya')
                }

                else{
                    items.value = response.data;
                    items.value.length > 0 ? showError.value = false : showError.value = true;
                }
            }
        });
    }
}

/**
 * When click in Item, show Dialog Component
 */
