<template>
    <v-sheet color="bgtopology" class="defaultCard cardReport" :class="smAndDown ? 'ma-1 pa-2' : 'pa-4 ma-4'">
        <!-- -->
        <v-row class="ma-0 mb-6 rowtitle" justify="start">           
            <v-img width="30" style="flex: 0.01 0 auto !important;" class="reportIMG" src="@/assets/Images/hcgroup.jpg"/>
            <h4 style="width: max-content" class="titleReport" v-if="smAndDown">
                {{ t('reportPage.title')}} 
                <span class="dateTitleSM d-print-none" v-if="dateini">[ {{ dateini }}</span> 
                <span class="dateTitleSM d-print-none" v-if="dateini && datefin"> - {{ datefin }}</span> 
                <span class="dateTitleSM d-print-none" v-if="dateini"> ]</span>
            </h4>
            <h1 style="width: max-content" class="titleReport" v-else>
                {{ t('reportPage.title')}} 
                <span class="dateTitle d-print-none" v-if="dateini">[ {{ dateini }}</span> 
                <span class="dateTitle d-print-none" v-if="dateini && datefin"> - {{ datefin }}</span> 
                <span class="dateTitle d-print-none" v-if="dateini"> ]</span>
            </h1>            
        </v-row>
        <div class="d-block">
            <h2 class="d-none d-print-block">
                Periodo: 
                <span class="dateTitle" v-if="dateini">[ {{ dateini }}</span> 
                <span class="dateTitle" v-if="dateini && datefin"> - {{ datefin }}</span> 
                <span class="dateTitle" v-if="dateini"> ]</span>
            </h2>
        </div>
        <div class="headerReport"> 
            <v-row>
                <v-col class="py-0" cols="12" sm="12" md="4" lg="4" xl="4">
                    <h4 class="mb-2">{{ t('datePickers.dateini')}}</h4>
                    <VueDatePicker 
                    class="mb-4"
                    v-model="dateini" 
                    :model-type="CONSTSTRINGS.STRINGS_HTML.time" 
                    :format="CONSTSTRINGS.STRINGS_HTML.time" />
                </v-col>
                <v-col class="py-0" cols="12" sm="12" md="4" lg="4" xl="4">
                    <h4 class="mb-2">{{ t('datePickers.datefin')}}</h4>
                    <VueDatePicker 
                    class="mb-4"
                    v-model="datefin" 
                    :model-type="CONSTSTRINGS.STRINGS_HTML.time" 
                    :format="CONSTSTRINGS.STRINGS_HTML.time" />
                </v-col>
                <v-col class="py-0" cols="12" sm="12" md="4" lg="4" xl="4">
                    <h4 class="mb-2">Tipo</h4>
                    <v-select
                    v-model="machineType"
                    density="compact"
                    variant="outlined"
                    :items="['Cliente', 'Acceso', 'Distribucion']"
                    >

                    </v-select>
                </v-col>
            </v-row>

            <v-row class="ma-0 mt-2">
                <v-btn 
                :disabled="!(datefin > dateini)" 
                class="mb-6" 
                color="btnSelect"
                @click="getItems">
                {{ t('buttons.searchbtn')}}
                </v-btn>
                <!-- <div class="pa-0 mb-6 ml-2"  v-if="markers.length > 0" style="width:max-content" > -->
                <!-- <div class="pa-0 mb-6 ml-2"  style="width:max-content" >
                    <v-btn
                    :disabled="btnDisabled || btnDisabledFirst" 
                    color="btnSelect" 
                    @click="exportToPdf">
                        Exportar a PDF
                    </v-btn>
                    <p v-if="btnDisabled" class="textDisable"> Cambiar tema para habilitar </p>
                </div> -->
            </v-row>
        </div>
        
        <div ref="reportPDF" id="reportPDFid">
            <v-row justify="space-around" class="mt-5 pdfrow" v-if="showInfo">
                <v-divider
                :thickness="10"
                class="border-opacity-75 divider_color d-none d-sm-none d-md-flex"
                ></v-divider>
                <v-col cols="12" sm="12" md="5" lg="5" xl="5" v-if="showInfo" align-self="center" class="colreport mx-n4 my-6">
                    <v-sheet class="defaultCard" color="white">
                        <h5 v-if="smAndDown" class="pl-4">{{ t('reportPage.subtitle1')}}  </h5>
                        <h3 v-else class="pl-4">{{ t('reportPage.subtitle1')}}  </h3>
                        <EasyDataTable
                        v-if="operability.length > 0"
                        header-class-name="headerColor"
                        theme-color="#0e2f5a"
                        :headers="headerOperabi"
                        :items="operability"          
                        alternating
                        show-index
                        hide-footer
                        >
                        <template #item-ok ="item">
                            {{item.ok}}%
                        </template>
                        <template #item-alarm ="item">
                            {{item.alarm}}%
                        </template>
                        <template #item-alert ="item">
                            {{item.alert}}%
                        </template>
                        <template #item-down ="item">
                            {{item.down}}%
                        </template>
                        <template #item-ip ="item">
                            <v-btn color='blue' variant="plain" @click="showDialogWithType(item.ip, item.hostname,item.tipo )">
                                {{item.ip}}
                            </v-btn>
                        </template>
                        </EasyDataTable>
                        <v-sheet class="text-center mt-6" color="transparent" v-else>
                            <Prog_circular />
                        </v-sheet>
                    </v-sheet>
                </v-col>
                <v-divider
                :thickness="10"
                class="border-opacity-75 divider_color d-none d-sm-none d-md-flex"
                vertical
                ></v-divider>
                <v-col cols="12" sm="12" md="5" lg="5" xl="5" v-if="showInfo" class="colreport2 mx-n4 my-6">
                    <v-sheet class="defaultCard " color="white">
                        <ColumnChart
                        class="pa-4 graphColumn3D"
                        v-if="cantOK.length > 0"
                        color="#000000"
                        :title="t('reportPage.subtitle1')"
                        name="Reporte"
                        :firstArray="cantOK"
                        :secondArray="cantALARM"
                        :thridArray="cantALERT"
                        :fourthArray="cantDOWN"
                        :arrayIPs="arrayIPs"
                        />
                        <v-sheet class="text-center mt-6" color="transparent" v-else>
                            <Prog_circular />
                        </v-sheet>
                    </v-sheet>
                </v-col>

                <v-divider
                :thickness="10"
                class="border-opacity-75 divider_color d-none d-sm-none d-md-flex"
                v-if="showTenLAT"
                ></v-divider>
                <v-col cols="12" sm="12" md="10" lg="10" xl="10" v-if="showTenLAT" class="colreport3 mx-n4 my-6">
                    <v-sheet class="defaultCard" color="white">
                        <h5 v-if="smAndDown" class="pl-4">Latencia</h5>
                        <h3 v-else class="pl-4">Latencia</h3>
                        <EasyDataTable
                        v-if="latencyTen.length > 0"
                        header-class-name="headerColor"
                        theme-color="#0e2f5a"
                        :headers="headerTenOperabi"
                        :items="latencyTen"          
                        alternating
                        show-index
                        hide-footer
                        >                           
                            <template #item-latencia ="item">
                                    {{item.latencia.toFixed(2)}}ms
                            </template>
                            <template #item-ip ="item">
                                <v-btn color='blue' variant="plain" @click="showDialogWithType(item.ip, item.name,item.tipo )">
                                    {{item.ip}}
                                </v-btn>
                            </template>
                        </EasyDataTable>
                        <v-sheet class="text-center mt-6" color="transparent" v-else>
                            <Prog_circular />
                        </v-sheet>
                    </v-sheet>  
                </v-col>
                <v-divider
                :thickness="10"
                class="border-opacity-75 divider_color d-none d-sm-none d-md-flex"
                v-if="showQualityLap"
                ></v-divider>
                <v-col cols="12" sm="12" md="10" lg="10" xl="10" v-if="showQualityLap" class="colreport3 mx-n4 my-6">
                    <v-sheet class="defaultCard" color="white">
                        <h5 v-if="smAndDown" class="pl-4">{{ t('reportPage.subtitle2')}}</h5>
                        <h3 v-else class="pl-4">{{ t('reportPage.subtitle2')}}</h3>
                        <EasyDataTable
                        v-if="qualityLap.length > 0"
                        header-class-name="headerColor"
                        theme-color="#0e2f5a"
                        :headers="headerQualLap"
                        :items="qualityLap"          
                        alternating
                        show-index
                        hide-footer
                        >
                            <template #item-linkradiohoriz ="item">
                                <v-row style="align-items: center !important">
                                    <v-col cols="2" class="pa-0"><span> {{ item.linkradiohoriz }} </span></v-col>
                                    <v-col cols="10">
                                        <v-progress-linear
                                        :model-value="-(item.linkradiohoriz)"
                                        height="10"
                                        striped
                                        :color="
                                        item.linkradiohoriz <= -85 ? '#ff0000' : item.linkradiohoriz <= -80 ? '#ff5050' : 
                                        item.linkradiohoriz <= -75 ? '#ff9900' : item.linkradiohoriz <= -70 ? '#ffff00' : 
                                        item.linkradiohoriz <= -65 ? '#ccff33' : item.linkradiohoriz <= -60 ? '#66ff33' :
                                        item.linkradiohoriz <= -55 ? '#40f7eb' : item.linkradiohoriz <= -50 ? '#3790fc' :
                                        item.linkradiohoriz <= -45 ? '#2d09f7' : item.linkradiohoriz <= -40 ? '#3b1ace' :
                                        item.linkradiohoriz <= -35 ? '#7009c4' : item.linkradiohoriz <= -33.6 ? '#8a079b' :'#c8c8c8'"
                                    ></v-progress-linear>
                                    </v-col>
                                </v-row>
                            </template>
                            <template #item-ip ="item">
                                <v-btn color='blue' variant="plain" @click="showDialogWithType(item.ip, item.name,item.tipo )">
                                    {{item.ip}}
                                </v-btn>
                            </template>
                        </EasyDataTable>
                        <v-sheet class="text-center mt-6" color="transparent" v-else>
                            <Prog_circular />
                        </v-sheet>
                    </v-sheet>  
                </v-col>
                <v-divider
                :thickness="10"
                class="border-opacity-75 divider_color d-none d-sm-none d-md-flex"
                v-if="showQualitySNRV || showQualitySNRH"
                ></v-divider>
                <v-col cols="12" sm="12" md="5" lg="5" xl="5" v-if="showQualitySNRV" class="colreport4 mx-n4 my-6">
                    <v-sheet class="defaultCard" color="white">
                        <h5 v-if="smAndDown" class="pl-4">{{ t('reportPage.subtitleV')}}</h5>                 
                        <h3 v-else class="pl-4">{{ t('reportPage.subtitleV')}}</h3>                 
                        <EasyDataTable
                        v-if="SNR_VERTI.length > 0"
                        header-class-name="headerColor"
                        theme-color="#0e2f5a"
                        :headers="headerSNRVert"
                        :items="SNR_VERTI"          
                        alternating
                        show-index
                        hide-footer
                        >
                        <template #item-vertical ="item">
                            <v-row justify="space-between" style="align-items: center !important">
                                <v-col cols="2" class="pa-0"><span> {{ item.vertical }} </span></v-col>
                                <v-col cols="10" class="pa-0 pr-2">
                                    <v-progress-linear
                                    :model-value="100"
                                    height="10"
                                    striped
                                    :color="
                                    item.vertical <= 15 ? '#FF0000' : item.vertical <= 20 ? '#FFEE00' :'#00FF00'"
                                ></v-progress-linear>
                                </v-col>
                            </v-row>
                        </template>
                        <template #item-ip ="item">
                            <v-btn color='blue' variant="plain" @click="showDialogWithType(item.ip, item.name, item.tipo )">
                                {{item.ip}}
                            </v-btn>
                        </template>
                        </EasyDataTable>
                        <v-sheet class="text-center mt-6" color="transparent" v-else>
                            <Prog_circular />
                        </v-sheet>
                    </v-sheet>  
                </v-col>
                <v-divider
                :thickness="10"
                class="border-opacity-75 divider_color d-none d-sm-none d-md-flex"
                vertical
                v-if="showQualitySNRH && showQualitySNRV"
                ></v-divider>
                <v-col cols="12" sm="12" md="5" lg="5" xl="5" v-if="showQualitySNRH" class="colreport5 mx-n4 my-6">
                    <v-sheet class="defaultCard card5" color="white">
                        <h5 v-if="smAndDown" class="pl-4">{{ t('reportPage.subtitleH')}}</h5>             
                        <h3 v-else class="pl-4">{{ t('reportPage.subtitleH')}}</h3>             
                        <EasyDataTable
                        v-if="SNR_HORIZ.length > 0"
                        header-class-name="headerColor"
                        theme-color="#0e2f5a"
                        :headers="headerSNRHorz"
                        :items="SNR_HORIZ"          
                        alternating
                        show-index
                        hide-footer
                        >
                        <template #item-horizontal ="item">
                            <v-row justify="space-between" style="align-items: center !important">
                                <v-col cols="2" class="pa-0"><span> {{ item.horizontal }} </span></v-col>
                                <v-col cols="10" class="pa-0 pr-6">
                                    <v-progress-linear
                                    :model-value="100"
                                    height="10"
                                    striped
                                    :color="
                                    item.horizontal <= 15 ? '#FF0000' : item.horizontal <= 20 ? '#FFEE00' :'#00FF00'"
                                ></v-progress-linear>
                                </v-col>
                            </v-row>
                        </template>
                        <template #item-ip ="item">
                            <v-btn color='blue' variant="plain" @click="showDialogWithType(item.ip, item.name, item.tipo )">
                                {{item.ip}}
                            </v-btn>
                        </template>
                        </EasyDataTable>
                        <v-sheet class="text-center mt-6" color="transparent" v-else>
                            <Prog_circular />
                        </v-sheet>
                    </v-sheet>  
                </v-col>
                <!-- <v-divider
                :thickness="10"
                class="border-opacity-75 divider_color d-none d-sm-none d-md-flex mb-4"
                v-if="showMAP"
                ></v-divider>
                <v-col cols="12" sm="12" md="10" lg="10" xl="10" v-if="showMAP" class="colreport6 mx-n4 my-6">
                    <v-sheet class="defaultCard" color="transparent">
                        <h3 v-if="smAndDown" class="pl-4">{{ t('reportPage.subtitle4')}} </h3>
                        <h3 v-else class="pl-4">{{ t('reportPage.subtitle4')}} </h3>
                    </v-sheet>                
                </v-col> -->
            </v-row>
        </div>
        
    </v-sheet>
    <!-- DIALOG COMPONENT -->
    <v-dialog v-model="dialog" width="85%">
        <DialogComponent
            :ip="selectedIp"
            :typeSel="selectedType"
            :name="selectedHostName"
            @dialog="closeDialog"
        />             
    </v-dialog>
</template>

<script setup lang="ts">
import axios from 'axios';
import { ref, computed, watch, Ref } from 'vue';
import { useDisplay } from 'vuetify';

import { useI18n }          from 'vue-i18n';
import CONSTSTRINGS            from '@/constants/strings';
import CONSTAPIS            from '@/constants/apis';

// Components
import ColumnChart          from '@/components/SmartLink/charts/ColumnChart.vue';
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import DialogComponent      from '@/components/SmartLink/dialog/dialogComponent.vue';

// Interfaces
import { ALIGN, LAT_REP,  GPS_REP_OBJ }    from '@/interfaces/SmartLink/Topology';
import { SNR }              from '@/interfaces/SmartLink/KPI';
import { ALLHEADER }       from '@/interfaces/SmartLink/Tables';
import { MARKERS }          from '@/interfaces/SmartLink/Map';
import { EQUIPAMENTS }    from '@/interfaces/SmartLink/Equips';

        //==================================================//
        //==================== VARIABLES ===================//
        //==================================================//

        /**
         * Vue / Vuetify
         */
        const { t }              = useI18n();        
        // const theme              = useTheme();
        const { smAndDown }  = useDisplay();

        /**
         * Component
         */
        const machineType                       = ref(''); 
        const latency:Ref<LAT_REP[]>            = ref([]);
        const gpsLatLng:Ref<GPS_REP_OBJ>        = ref({});
        const markers:Ref<MARKERS[]>            = ref([]);
        // const markersHeatmap:Ref<MARKERS[]>     = ref([]);
        const operability:Ref<SNR[]>            = ref([]);
        // const qualityLap:Ref<KPI[]>             = ref([]);
        const qualityLap:Ref<any[]>             = ref([]);
        const latencyTen:Ref<EQUIPAMENTS[]>             = ref([]);
        const SNR_VERTI:Ref<SNR[]>          = ref([]);
        const SNR_HORIZ:Ref<SNR[]>          = ref([]);
        const showInfo                          = ref(false);
        const showTenLAT                        = ref(false);
        const showMAP                           = ref(false);
        const showQualityLap                    = ref(true);
        const showQualitySNRV                   = ref(true);
        const showQualitySNRH                   = ref(true);
        const dateini                           = ref('');
        const datefin                           = ref('');
        const cantOK:Ref<number[]>              = ref([]);
        const cantALARM:Ref<number[]>           = ref([]);
        const cantALERT:Ref<number[]>           = ref([]);
        const cantDOWN:Ref<number[]>            = ref([]);
        const arrayIPs:Ref<string[]>            = ref([]);
        const reportPDF                         = ref();
        const headerQualLap                     = ref();
        const headerSNRVert                     = ref();
        const headerSNRHorz                     = ref();
        const headerOperabi                     = ref();
        const headerTenOperabi                  = ref();
        const allIPsfromMap:Ref<string[]>       = ref([]);
        const getPetition:Ref<boolean>          = ref(false);
        // const btnDisabled                       = computed(()=> theme.global.name.value === 'darkCustom');
        const btnDisabledFirst                  = ref(true);

        const HEADERS_TABLE_KPI:ALLHEADER      = {           
            quality_lap : [
                { text: computed(() => t('kpiPage.headers.general.time')),              value: "fecha",         sortable: false},
                { text: "IP",                                                           value: "ip",            sortable: true },
                { text: computed(() => t('kpiPage.headers.general.hostname')),          value: "name",          sortable: true },
                { text: computed(() => t('kpiPage.headers.quality_lap.linkradiohoriz')),value: "link_radio_rx", sortable: true },
                { text: "BER",                                                          value: "avgpower",      sortable: true },
            ],
            snr_vertical : [
                { text: computed(() => t('kpiPage.headers.general.time')),              value: "fecha",         sortable: false},
                { text: "IP",                                                           value: "ip",            sortable: true },
                { text: computed(() => t('kpiPage.headers.general.hostname')),          value: "name",          sortable: true },
                { text: "SNR Vertical",                                                 value: "snr_v",         sortable: true },
                { text: "BER",                                                          value: "avgpower",      sortable: true },
            ],  
            snr_horizontal : [
                { text: computed(() => t('kpiPage.headers.general.time')),              value: "fecha",         sortable: false},
                { text: "IP",                                                           value: "ip",            sortable: true },
                { text: computed(() => t('kpiPage.headers.general.hostname')),          value: "name",          sortable: true },
                { text: "SNR Horizontal",                                               value: "snr_h",         sortable: true },
                { text: "BER",                                                          value: "avgpower",      sortable: true },
            ],  
            operability   : [               
                { text: "IP",                                                           value: "ip",            sortable: true },
                { text: computed(() => t('kpiPage.headers.general.hostname')),          value: "name",          sortable: true },
                { text: "Normal",                                                       value: "ok",            sortable: true },
                { text: computed(() => t('labels.alert')),                              value: "alert",         sortable: true },
                { text: computed(() => t('labels.alarm')),                              value: "alarm",         sortable: true },
                { text: computed(() => t('labels.down')),                               value: "down",          sortable: true },                
            ],
            tenlatency   : [               
                { text: "IP",                                                           value: "ip",            sortable: true },
                { text: computed(() => t('kpiPage.headers.general.hostname')),          value: "name",          sortable: true },
                { text: "TIPO",                                                         value: "tipo",          sortable: true },
                { text: "Latency",                                                      value: "latencia",      sortable: true },
            ],

        };

        /**
         * DIALOG
         */
        const dialog                        = ref(false);
        const selectedIp                    = ref('');
        const selectedHostName              = ref('');
        const selectedType                  = ref('');

        /**
         * When click in Item, show Dialog Component
         */
        const showDialogWithType = (ip:string, name:string, type:string) => {
            selectedIp.value        = ip;
            selectedHostName.value  = name;
            selectedType.value      = type;
            dialog.value            = true;
        }

        const closeDialog = (value:boolean) => {
            dialog.value = value;
        }


        //==================================================//
        //====================== WATCH =====================//
        //==================================================//

        /**
         * Hidden Report Tables and Maps
         */
        watch(dateini,(p)=>{

            if(p === null){
                showInfo.value      = false;
                markers.value       = [];
            }

        })

        /**
         * Hidden Report Tables and Maps
         */
        watch(datefin,(p)=>{

            if(p === null){
                showInfo.value      = false;
                markers.value       = [];
            }
            
        });


        //==================================================//
        //===================== METHODS ====================//
        //==================================================//

        /**
         * Get Items
         * (Operability, Qualitylap, Qualitysnr, Latency, GPSLatLng)
         */
        const getItems = async() => {

            btnDisabledFirst.value = true;

            headerQualLap.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.quality_lap];
            headerSNRVert.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.snr_vertical];
            headerSNRHorz.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.snr_horizontal];
            headerOperabi.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.operability];
            headerTenOperabi.value  = HEADERS_TABLE_KPI['tenlatency'];

            latency.value       = [];
            gpsLatLng.value     = {};
            markers.value       = [];
            operability.value   = [];
            qualityLap.value    = [];
            latencyTen.value    = [];
            SNR_VERTI.value     = [];
            SNR_HORIZ.value     = [];
            arrayIPs.value      = [];
            cantOK.value        = [];
            cantALARM.value     = [];
            cantALERT.value     = [];
            cantDOWN.value      = [];
            showInfo.value      = true;
            showMAP.value       = true; 
            allIPsfromMap.value = [];
            getPetition.value   = false;

           let params ={
                dateini : dateini.value ,
                datefin : datefin.value ,
                ip      : allIPsfromMap.value,
                tipo    : CONSTSTRINGS.STRINGS_VIS_PAGES.report,
                macType : machineType.value,
            }

            /**
             * Get SNR
             */
            axios.get(CONSTAPIS.APIS.report.operability,{params:params}).then(async(res)=>{
                let allData             = res.data;
                let finalArray:SNR[]    = [];
                await allData.forEach((elem:SNR)=>{
                    let cantTotal   = (elem.ok??0) + (elem.alarm??0) + (elem.alert??0) + (elem.down??0)
                    let finalItem   = {
                        ip      : elem.ip,
                        name    : elem.name,
                        tipo    : elem.tipo,
                        ok      : parseFloat(((elem.ok??0)/cantTotal*100).toFixed(2)),
                        alert   : parseFloat(((elem.alert??0)/cantTotal*100).toFixed(2)),
                        alarm   : parseFloat(((elem.alarm??0)/cantTotal*100).toFixed(2)),
                        down    : parseFloat(((elem.down??0)/cantTotal*100).toFixed(2)),
                    }
                    finalArray.push(finalItem)   
                                        
                })
                finalArray.sort((a:SNR, b:SNR) => (b.alarm??0) - (a.alert??0))
                let finalData = finalArray.slice(0,10);
                operability.value = finalData;
                operability.value.forEach((item:SNR)=>{
                    cantOK.value.push((item.ok??0));
                    cantALARM.value.push((item.alert??0));                
                    cantALERT.value.push((item.alarm??0));                
                    cantDOWN.value.push((item.down??0));
                    arrayIPs.value.push((item.name??''));
                })
            })

            /**
             * Get QUALITY_LAP
             */
            axios.get(CONSTAPIS.APIS.report.qualitylap,{params:params}).then(async(res)=>{
                
                let finalArray:ALIGN[]  = [];
                let allData         = res.data;
                if(allData.length > 0){
                    allData.forEach((elem:any) => {
                        let allRARIO    = JSON.parse(elem.link_radio??`{"tx": 0, "rx":0}`);
                        let RADIOrx       = (typeof allRARIO['rx']).toUpperCase() == 'OBJECT' && allRARIO['rx'] !== null  ? allRARIO['rx'][0] : allRARIO['rx'];
                        
                        let allAVG      = JSON.parse(elem.avgpower??`{"tx": 0, "rx":0}`);
                        let AVGtx       = (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null  ? allAVG['tx'][0] : allAVG['tx'];
                        let finalItem = {
                            fecha           : elem.fecha,
                            name            : elem.name,
                            ip              : elem.ip,
                            avgpower        : AVGtx??0,
                            link_radio_rx   : RADIOrx??0
                        }
                        finalArray.push(finalItem)
                    })
                    showQualityLap.value    = true;
                    let finalData           = finalArray.slice(0,5);
                    qualityLap.value        = finalData;
                }else{
                    showQualityLap.value    = false;
                }
            })

            axios.get(CONSTAPIS.APIS.report.tenlatency,{params:params}).then(async(res)=>{
                
                let allData         = res.data;
                if(allData.length > 0){
                    showTenLAT.value    = true;
                    let totalData = allData.sort((a:EQUIPAMENTS, b:EQUIPAMENTS) => (b.lat??0) - (a.lat??0))
                    latencyTen.value        = totalData;
                }else{
                    showTenLAT.value    = false;
                }
            })

            /**
             * Get QUALITY_SNR
             */
            axios.get(CONSTAPIS.APIS.report.qualitysnr,{params:params}).then(async(res)=>{
                let allData         = res.data;
                let finalArray_VER:SNR[]  = [];
                let finalArray_HOR:SNR[]  = [];

                allData.forEach((elem:ALIGN)=>{
                    let allSNR      = JSON.parse(elem.snr??`{"V": 0, "H": 0}`);
                    let SNRV        = (typeof allSNR['V']).toUpperCase() == 'OBJECT' && allSNR['V'] !== null ? allSNR['V'][0] : allSNR['V'];
                    let SNRH        = (typeof allSNR['H']).toUpperCase() == 'OBJECT' && allSNR['H'] !== null ? allSNR['H'][0] : allSNR['H'];

                    let allAVG      = JSON.parse(elem.avgpower??`{"tx": 0, "rx":0}`);
                    let AVGtx       = (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null ? allAVG['tx'][0] : allAVG['tx'];
                    let finalItemSNRH:SNR = {
                        fecha       : elem.fecha??'',
                        name        : elem.name??'',
                        ip          : elem.ip??'',
                        snr_h       : SNRH??0,
                        tipo        : elem.tipo??'',
                        avgpower    : AVGtx??0
                    }
                    let finalItemSNRV:SNR = {
                        fecha       : elem.fecha??'',
                        name        : elem.name??'',
                        ip          : elem.ip??'',
                        snr_v       : SNRV??0,
                        tipo        : elem.tipo??'',
                        avgpower    : AVGtx??0
                    }
                    finalArray_VER.push(finalItemSNRV);
                    finalArray_HOR.push(finalItemSNRH);
                })

                finalArray_VER.sort((a:SNR, b:SNR) =>( a.snr_v??0) - (b.snr_v??0));
                finalArray_HOR.sort((a:SNR, b:SNR) =>( a.snr_h??0) - (b.snr_h??0));
                let finalData_VER = finalArray_VER.slice(0,5);
                let finalData_HOR = finalArray_HOR.slice(0,5);
                SNR_VERTI.value = finalData_VER;
                SNR_HORIZ.value = finalData_HOR;

                SNR_VERTI.value.length > 0 ? showQualitySNRV.value = true : showQualitySNRV.value = false;
                SNR_HORIZ.value.length > 0 ? showQualitySNRH.value = true : showQualitySNRH.value = false;
            })

            btnDisabledFirst.value = false;
        }

</script>

