<template>
    <v-sheet class="defaultCard" color="bgtopology">
        <v-row justify="space-between" class="ma-0 mb-4">

            <!-- TITLE VIEW -->
            <v-col class="pa-0" cols="12" sm="4" md="7" lg="7" xl='8' align-self="center" >                
                <span  class="titlePages">{{ title }}</span>
            </v-col>
             
            <!-- SEARCH FIELD -->
            <v-col class="pa-0" cols="12" sm="8" md="5" lg="5" xl='4' style="text-align: -webkit-right;" >
                <v-text-field
                    v-if="items.length !== 0"
                    :style="!smAndDown ? 'max-width: 400px !important; margin-top: 20px':' margin-top: 10px'"             
                    v-model="searchValue"
                    :label="t('search')"
                    variant="outlined"
                    density="compact" 
                    hide-details 
                ></v-text-field>
            </v-col>
        </v-row>
        <v-row v-if="items.length !== 0" class="my-4">
            <v-col v-for="(item,i ) in finalLegend" :key="i">
                <rangeComponent
                    :principal-color="item.prinColor "
                    :secundary-color="item.secuColor "
                    :color-text-princ="item.colorText1"
                    :color-text-secun="item.colorText2"
                    :principal-text="item.prinText"
                    :secundary-text="item.secuText"
                    />
            </v-col>
        </v-row>

        <!-- TABLE COMPONENT -->
        <EasyDataTable
        v-if="items.length !== 0"
        table-class-name="customize-table"
        :search-field="searchField"
        :search-value="searchValue"
        :headers="finalHeader"
        :items="items"
        :class="finalLegend === null ? 'pt-10': ''"
        show-index
        @click-row="showDialog"
        >       
            <template #item-link_radio_rx ="item">
                <v-row style="align-items: center !important">
                    <span> {{ item.link_radio_rx }} </span>
                    <v-col cols="10">
                        <v-progress-linear
                        :model-value="-(item.link_radio_rx)"
                        height="10"
                        striped
                        :color=" item.link_radio_rx <= -35 && item.link_radio_rx > -50 ? CONSTCOLORS.COLOR_HTML.grey : 
                        item.link_radio_rx <= -35 && item.link_radio_rx > -50 ? CONSTCOLORS.COLOR_HTML.green : 
                        item.link_radio_rx <= -50 && item.link_radio_rx > -70 ? CONSTCOLORS.COLOR_HTML.yellow : CONSTCOLORS.COLOR_HTML.red "
                        
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-vertical ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.vertical }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-2">
                        <v-progress-linear
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.vertical <= 15 ? '#FF0000' : item.vertical <= 20 ? '#FFEE00' :'#00FF00'"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-horizontal ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.horizontal }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-6">
                        <v-progress-linear
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.horizontal <= 15 ? '#FF0000' : item.horizontal <= 20 ? '#FFEE00' :'#00FF00'"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-cantOK ="item">
                {{item.cantOK}}%
            </template>
            <template #item-cantAlarm ="item">
                {{item.cantAlarm}}%
            </template>
            <template #item-cantAlert ="item">
                {{item.cantAlert}}%
            </template>
            <template #item-cantDown ="item">
                {{item.cantDown}}%
            </template>
            <template #item-latencia ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.latencia }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-6">
                        <v-progress-linear
                        style="border-radius: 20px;"
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.latencia < 0 ? '#9E9E9E' : item.latencia <= 100 ? '#00FF00' : item.latencia <= 500 ? '#FFEE00' :'#FF0000'"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>
        </EasyDataTable>

        <!-- DIALOG COMPONENT -->
        <v-dialog v-model="dialog" width="85%">
            <DialogComponent
             :ip="selectedIp"
             :typeSel="selectedType"
             :name="selectedName"
             @dialog="closeDialog"
             :status="'Ok'"
            />             
        </v-dialog>

        <v-row  v-if="showError" class="ma-0 px-6 py-3">
            <h3 v-if="getValueStoreKPI === 'latency'"> {{t('noLatProbl')}} </h3>
            <h3 v-else-if="getValueStoreKPI === 'temp_inst'"> {{t('noTemProbl')}} </h3>
            <h3 v-else> {{t('noInformation')}} </h3>
        </v-row>

    </v-sheet>

</template>

<script setup lang="ts">
import { computed, onMounted, ref, watch, Ref, ComputedRef  } from 'vue'
import axios                        from 'axios';
import { useStore }                 from 'vuex';
import { useDisplay }               from 'vuetify';
import { useI18n }                  from 'vue-i18n';

// COMPONENTS
import DialogComponent              from '@/components/SmartLink/dialog/dialogComponent.vue';
import rangeComponent from '@/components/SmartLink/dashboard/rangeComponent.vue';


// CONSTANTS
import CONSTAPIS                    from '@/constants/apis';
import CONSTSTRINGS                 from '@/constants/strings';
import CONSTCOLORS                  from '@/constants/colors';
import { FINAL_TYPE_LEGEND }        from '@/constants/legends';
import { FINAL_HEADERS_TABLE_KPI }  from '@/constants/tableHeaders';

// INTERFACES
import { STRINGS, SNR, KPI}         from '@/interfaces/SmartLink/KPI';
import { HEADER }    from '@/interfaces/SmartLink/Tables'
import { ALIGN }                    from '@/interfaces/SmartLink/Topology';
import { LEGEND }     from '@/interfaces/SmartLink/General';

//FUNCTIONS 
import { getFinalDate } from '@/functions/project'


const { t }                         = useI18n();
const store                         = useStore();
const { smAndDown }                 = useDisplay();

/**
 * Component
 */

const getValueStoreKPI:ComputedRef<string>              = computed (()=>{
    return store.state.valueKPI
});
 
const legend:ComputedRef<LEGEND[]>  = computed(() => FINAL_TYPE_LEGEND[getValueStoreKPI.value]);
const finalLegend     = ref();

const headers:ComputedRef<HEADER[]> = computed(() => FINAL_HEADERS_TABLE_KPI[getValueStoreKPI.value]);
const finalHeader:Ref<HEADER[]>     = ref([]); 


const searchField                   = ref();
const searchValue                   = ref("");
const items:Ref<SNR[]>              = ref([]); 
const dialog                        = ref(false);
const selectedIp                    = ref('');
const selectedName                  = ref('');
const selectedType                  = ref('');
const toDay                         = ref(new Date());
const thirtyDaysAgo                 = ref(toDay.value.getTime() - (1000 * 60 * 60 * 24 * 30));
const daysAgo                       = ref(new Date(thirtyDaysAgo.value));
const finalEndDate                  = getFinalDate(toDay.value);
const finalStartDate                = getFinalDate(daysAgo.value);

const showError                     = ref(false);  
// 
const TITLE_KPI:ComputedRef<STRINGS>=  computed(() => {
    return {
        latency         : t('kpiPage.titles.latency'),
        quality_lap     : t('kpiPage.titles.quality_lap'),
        quality_snr     : t('kpiPage.titles.quality_snr'),
        pack_inst       : t('kpiPage.titles.pack_inst'),
        temp_inst       : t('kpiPage.titles.temp_inst'),
        wire_inst       : t('kpiPage.titles.wire_inst'),
        operability     : t('kpiPage.titles.operability') + ' ['+finalStartDate.toString() + ' - '+finalEndDate.toString() +'] ',
        wiredpeers      : t('kpiPage.titles.wiredpeers'),
        wirelesspeers   : t('kpiPage.titles.wirelesspeers'),
    }
});


const title                         = computed(() => TITLE_KPI.value[getValueStoreKPI.value]??'Title');

//==================================================//
//===================== METHODS ====================//
//==================================================//

/**
 * GET ALL DATA
 */
const getDataTable = async () =>{
    showError.value = false;

    items.value         = [];
    finalHeader.value       = headers.value;
    finalLegend.value       = legend.value;
    let params          = { kpi : getValueStoreKPI.value };

    if(getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.wired ||
    getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.wireless){
        let getAPI = getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.wired ? CONSTAPIS.APIS.kpi.getcostwired : CONSTAPIS.APIS.kpi.getcostwireless;
        await axios.get(getAPI).then(async (response) =>{ 

            let itemsCost = response.data;
            let finalArray:SNR[]  = [];
            itemsCost.forEach((elem:KPI) => {
                let instamesh = JSON.parse(elem.wireless??'');
                let finalItems = {
                    fecha      : elem.fecha,
                    ip         : elem.ip,
                    name       : elem.name,
                    costo      : instamesh.cost ? instamesh.cost : 0
                }
                finalArray.push(finalItems);
            })
            items.value = finalArray;
            // items.value = response.data;
            items.value.length > 0 ? showError.value = false : showError.value = true;
            })
    } else {
        await axios.get(CONSTAPIS.APIS.kpi.getAllDataKPI,{params:params}).then(async (response) =>{
            if(getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.operability){
                let allData             = response.data;
                let finalArray:SNR[]    = [];
                showError.value         = false;
                
                if(allData.length > 0){
                    await allData.forEach((elem:SNR)=>{
                        let cantTotal   = (elem.ok??0) + ( elem.alarm??0) + (elem.alert??0) + (elem.down??0)
                        let finalItem   = {
                            ip          : elem.ip,
                            name        : elem.name,
                            tipo        : elem.tipo,
                            cantOK      : parseFloat(((elem.ok??0)/cantTotal*100).toFixed(2)),
                            cantAlert   : parseFloat(((elem.alert??0)/cantTotal*100).toFixed(2)),
                            cantAlarm   : parseFloat(((elem.alarm??0)/cantTotal*100).toFixed(2)),
                            cantDown    : parseFloat(((elem.down??0)/cantTotal*100).toFixed(2)),
                        }
                        finalArray.push(finalItem)                       
                    })
                    finalArray.sort((a:SNR, b:SNR) => (b.alarm??0) - (a.alarm??0))
                    items.value = finalArray;
                }
                else{ showError.value = true }
                
            }else{
                if(getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.qualitysnr){
                    let allData             = response.data;
                    let finalArray:SNR[]    = [];
                    if(allData.length > 0){
                        allData.forEach((elem:ALIGN)=>{
                            let allSNR      = JSON.parse(elem.snr??`{"V": 0, "H": 0}`);
                            let SNRV        = (typeof allSNR['V']).toUpperCase() == 'OBJECT' && allSNR['V'] !== null ? allSNR['V'][0] : allSNR['V'];
                            let SNRH        = (typeof allSNR['H']).toUpperCase() == 'OBJECT' && allSNR['H'] !== null ? allSNR['H'][0] : allSNR['H'];

                            let allAVG      = JSON.parse(elem.avgpower??`{"tx": 0, "rx":0}`);
                            // let AVGrx       = (typeof allAVG['rx']).toUpperCase() == 'OBJECT' ? allSNR['rx'][0] : allSNR['rx'];
                            let AVGtx       = (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null ? allAVG['tx'][0] : allAVG['tx'];
                            let finalItem:SNR = {
                                fecha       : elem.fecha??'',
                                name        : elem.name??'',
                                ip          : elem.ip??'',
                                snr_h       : SNRH??0,
                                snr_v       : SNRV??0,
                                tipo        : elem.tipo??'',
                                avgpower    : AVGtx??0
                            }
                            finalArray.push(finalItem) 
                        })
                        // finalArray.sort((a:SNR, b:SNR) => (a.snr_v??0) - (b.snr_v??0))
                        items.value = finalArray;
                    }
                    else{ showError.value = true } 
                }
                else if(getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.quality){
                    let allData         = response.data;
                    let finalArray:ALIGN[]  = [];
                    if(allData.length > 0){
                        allData.forEach((elem:ALIGN)=>{
                            let allRARIO    = JSON.parse(elem.link_radio??`{"tx": 0, "rx":0}`);
                            let RADIOrx       = (typeof allRARIO['rx']).toUpperCase() == 'OBJECT' && allRARIO['rx'] !== null  ? allRARIO['rx'][0] : allRARIO['rx'];
                            // let RADIOtx       = (typeof allRARIO['tx']).toUpperCase() == 'OBJECT' ? allRARIO['tx'][0] : allRARIO['tx'];
                            let allAVG      = JSON.parse(elem.avgpower??`{"tx": 0, "rx":0}`);
                            let AVGtx       = (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null  ? allAVG['tx'][0] : allAVG['tx'];
                            let finalItem = {
                                fecha           : elem.fecha,
                                name            : elem.name,
                                ip              : elem.ip,
                                avgpower        : AVGtx??0,
                                link_radio_rx   : RADIOrx??0,
                                tipo            : elem.tipo??'',
                            }
                            finalArray.push(finalItem) 
                        })
                        items.value = finalArray;
                    }
                    else{  showError.value = true }
                }
                

                else if(getValueStoreKPI.value === CONSTSTRINGS.STRING_KPI.temp_inst){
                    let itemsTemp = response.data;
                    let finalArray:SNR[]  = [];
                    itemsTemp.forEach((elem:KPI) => {
                        let temp = JSON.parse(elem.valores??'');
                        let finalItems = {
                            fecha      : elem.fecha,
                            ip         : elem.ip,
                            name       : elem.name,
                            temperatura: `${(temp.temperature[0]/100).toFixed(2)} °C`
                        }
                        finalArray.push(finalItems);
                    })
                    items.value = finalArray;
                    items.value.length > 0 ? showError.value = false : showError.value = true;
                    // console.log('hoya')
                }

                else{
                    items.value = response.data;
                    items.value.length > 0 ? showError.value = false : showError.value = true;
                }
            }
        });
    }
}

/**
 * When click in Item, show Dialog Component
 */
const showDialog = (item:SNR) => {
    selectedIp.value    = item.ip??''; 
    selectedType.value  = item.tipo??'';
    selectedName.value  = item.name??'';
    dialog.value        = true;
    
    // if(item.IPv4){ 
    //     selectedIp.value    = item.IPv4; 
    //     selectedType.value  = getValueStoreKPI.value;
    // }
}

const closeDialog = (value:boolean) => {
    dialog.value = value;
}


//==================================================//
//====================== WATCH =====================//
//==================================================//

watch(getValueStoreKPI,()=>{
    getDataTable()       
})

onMounted(() => {
    getDataTable()
})
</script>

<style>

.table_kpi{
    border: 1px solid rgba(var(--v-theme-colorBorder),0.1)!important;
    border-radius: 20px !important;
    margin: 20px;
}

.btnIpKPI{
    color: rgb(var(--v-theme-colorText));
    /* line-height: 40px; */
    transition: 0.3s;
    font-weight: bold;
}
.btnIpKPI:hover{
    cursor: pointer;
    scale: 1.02;
    transition: 0.3s;
}

</style>